.footer {
  font-family: 'FuturaBook';
  padding-top: 50px;
  &-footer {
    font-size: 22px;
    text-align: center;
    padding: 24px 0;
  }
  &-container {
    @extend %container;
    flex-direction: column;
  }
  &-title {
    font-size: 4vw;
    font-family: 'FuturaCondensedExtraBold';
    color: #fcbb1b;
    text-transform: uppercase;
    line-height: 1.2em;  
    @media( max-width: $screen-xs-max ) {
      font-size: 34px;
    }
  }
  &-flex {
    display: flex;
    @media( max-width: $screen-xs-max ) {
      flex-direction: column;
    }      
  }
  &-column {
    width: 50%;
    flex-basis: 50%;
    @media( max-width: $screen-xs-max ) {
      width: 100%;
      flex-basis: 100%;
      margin-bottom: 24px;
    }
  }
  &-form {
    font-size: 18px;
    line-height: 1.8em;
    font-size: 1.5vw;
    position: relative;
    @media( max-width: $screen-xs-max ) {
      font-size: 16px;
    } 
    &-loading, &-success {
      background-color: white;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      pointer-events: none;
      transition: opacity 500ms ease-out;
      &.active {
        opacity: 1;
        pointer-events: all;
      }
    }   
    &-loading {
      display: flex;
      align-content: center;
      justify-content: center;
      .loader {
        max-width: 100px;
        @media( max-width: $screen-xs-max ) {
          max-width: 50px;
        }      
      }
    }  
    &-success {
      font-family: 'FuturaCondensedExtraBold';
      font-size: 2vw;
      line-height: 1.2em;
      text-transform: uppercase;
      @media( max-width: $screen-xs-max ) {
        font-size: 20px;
      }
    }
    &-input {
      border-bottom: 2px solid #fcbb1b;
      border-radius: 0;
      &.parsley-error {
        border-bottom-color: red;
        color: red;
      }
    }
    &-select {
      font-family: 'FuturaBook';
      background-color: white;
      text-transform: uppercase;
      padding-right: 30px;
      background-image: url('../images/logan-dropdown.svg');
      background-repeat: no-repeat;
      background-position: right center;
      background-size: 25px;
      cursor: pointer;
    }
    &-textarea {
      font-family: 'FuturaBook';
      padding: 16px;
      border: 2px solid #fcbb1b;
      border-radius: 0;
      max-width: 100%;
      width: 100%;
      &.parsley-error {
        border-color: red;
      }
    }
    &-column {
      &:last-child {
        display: flex;
      }
    }
    &-submit {
      font-family: 'FuturaCondensedExtraBold';
      letter-spacing: 2px;
      text-transform: uppercase;
      padding: 8px;
      border: 3px solid black;
      border-radius: 0;
      margin-left: auto;
      transition: all 250ms ease-out;
      cursor: pointer;
      font-size: 1.5vw;
      @media( max-width: $screen-xs-max ) {
        font-size: 20px;
      }
      &:hover {
        color: #fcbb1b;
        border-color: #fcbb1b;
      }
    }
  }
  &-social {
    display: flex;
    justify-content: flex-end;
    @media( min-width: $screen-sm-min ) {
      flex-direction: column;
    }
    @media( max-width: $screen-xs-max ) {
      justify-content: space-between;
    }      
    &-wrapper {
      display: flex;
      align-items: flex-end;
      flex-direction: column;
      &.chat {
        @media( max-width: $screen-xs-max ) {
          align-items: flex-start;
        }   
      }
    }
    &-list {
      display: flex;
    }
    &-item {
      margin-left: 8px;
    }
    &-title {
      font-family: 'FuturaCondensedExtrabold';
      text-transform: uppercase;
      font-size: 28px;
      @media( max-width: $screen-xs-max ) {
        font-size: 22px;
      }        
    }
    &-svg {
      width: 50px;
      height: 50px;
      transition: fill 250ms ease-out;
      @media( max-width: $screen-xs-max ) {
        width: 30px;
        height: 30px;
      }
      &:hover {
        fill: #fcbb1b;
      }
    }
  }
}