$columns: 12;
$gutter-width: 24px;
$gutter-padding: $gutter-width / 2;

$sizes: (
	xxs: $screen-xxs-min,
	xs: $screen-xs-min,
	sm: $screen-sm-min,
	md: $screen-md-min,
	lg: $screen-lg-min,
	xl: $screen-xl-min,
);

$container-width-xl: 1250px;
$container-width-lg: 1040px;
$container-width-md: 952px;
$container-width-sm: 728px;

@for $i from 1 to 21 {
  .air-#{$i} {
		margin-bottom: $gutter-padding * $i !important;
  }
}

// START - DISPLAYS

$sizes-display: (
	xxs: (
		hiddenUp: $screen-xxs-min,
		hiddenDown: $screen-xxs-max
	),
	xs: (
		hiddenUp: $screen-xs-min,
		hiddenDown: $screen-xs-max
	),
	sm: (
		hiddenUp: $screen-sm-min,
		hiddenDown: $screen-sm-max
	),
	md: (
		hiddenUp: $screen-md-min,
		hiddenDown: $screen-md-max
	),
	lg: (
		hiddenUp: $screen-lg-min,
		hiddenDown: $screen-lg-max
	),
	xl: (
		hiddenUp: $screen-xl-min,
		hiddenDown: 1980px
	)
);

@each $size, $mediaquery in $sizes-display {
	.hidden-#{$size}-up {
		@media( min-width: map-get($mediaquery, hiddenUp)) {
			display: none !important;
		}
  }
	.hidden-#{$size}-down {
		@media( max-width: map-get($mediaquery, hiddenDown)) {
			display: none !important;
		}
	}
}
