.technology {
  padding-top: 125px;
  @extend %container;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  &-flex {
    display: flex;
    position: relative;
    @media( max-width: $screen-xs-max ) {
      flex-direction: column;
    }      
  }
  &-column {
    width: 50%;
    flex-basis: 50%;
    @media( max-width: $screen-xs-max ) {
      width: 100%;
      margin-bottom: 24px;
    }
    &.fix {
      @media( min-width: $screen-sm-min ) {
        padding-right: 50px;
      }
      @media( min-width: $screen-md-min ) {
        padding-right: 100px;
      }
    }
  }
  &-title {
    color: #1f355e;
    font-size: 5vw;
    @media( min-width: $screen-sm-min )and ( max-width: $screen-md-max ) {
      font-size: 7vw;
    }
    @media( max-width: $screen-xs-max ) {
      font-size: 40px;
    }
  }
  &-title, &-subtitle {
    font-family: 'FuturaCondensedExtraBold';
    text-transform: uppercase;
    z-index: 1;
    position: relative;
    margin-bottom: 8px;
    @media( max-width: $screen-xs-max ) {
      font-size: 28px;
    }      
  }
  &-subtitle {
    line-height: 1.2em;
    min-height: 2.4em;
    font-size: 2vw;
    @media( min-width: $screen-sm-min )and ( max-width: $screen-md-max ) {
      font-size: 3vw;
    }
    @media( max-width: $screen-xs-max ) {
      margin-bottom: 16px;
      font-size: 20px;
      min-height: 0;
    }
  }
  &-description {
    font-family: 'FuturaBook';
    z-index: 1;
    position: relative;
    font-size: 1.4vw;
    line-height: 1.4em;
    @media( min-width: $screen-sm-min )and ( max-width: $screen-md-max ) {
      font-size: 2.3vw;
    }
    @media( max-width: $screen-xs-max ) {
      font-size: 16px;
    }
  }
  .graphic {
    width: 100%;
    max-width: 416px;
    margin: 0 auto;
  }
  &-phones {
    width: 230px;
    height: 626px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url('../images/smartphones.gif');
  }
  &-footer {
    @extend %container;
    flex-direction: column;
    font-family: 'FuturaBook';
    &-title {
      font-family: 'FuturaCondensedExtraBold';
      font-size: 20px;
      margin-bottom: 8px;
      text-transform: uppercase;
      @media( min-width: $screen-sm-min ) {
        font-size: 34px;
      }
    }
    &-flex {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media( max-width: $screen-xs-max ) {
        flex-wrap: wrap;
      }
    }
    &-item {
      width: 50%;
      @media( max-width: $screen-xs-max ) {
        text-align: center;
        margin-bottom: 24px;
      }
      &:nth-child(3) {
        width: 100%;
      }
      img {
        max-width: 60%;
        @media( max-width: $screen-xs-max ) {
          max-width: 40%;
        }
      }
    }
  }
  &-list-logo {
    max-width: 200px;
    @media( max-width: $screen-xs-max ) {
      margin-bottom: 0 !important;
    }
  }
}