$facebook-icon-white: url("data:image/svg+xml;base64,");
$facebook-icon-grey: url("data:image/svg+xml;base64,");
$facebook-icon-color: url("data:image/svg+xml;base64,");
$twitter-icon-white: url("data:image/svg+xml;base64,");
$twitter-icon-grey: url("data:image/svg+xml;base64,");
$twitter-icon-color: url("data:image/svg+xml;base64,");
$instagram-icon-white: url("data:image/svg+xml;base64,");
$instagram-icon-grey: url("data:image/svg+xml;base64,");
$instagram-icon-color: url("data:image/svg+xml;base64,");
$linkedin-icon-white: url("data:image/svg+xml;base64,");
$linkedin-icon-grey: url("data:image/svg+xml;base64,");
$linkedin-icon-color: url("data:image/svg+xml;base64,");

$social-networks: (
	facebook: (
		backgroundImageWhite: $facebook-icon-white,
		backgroundImageGrey: $facebook-icon-grey,
		backgroundImageColor: $facebook-icon-color,
	),
	twitter: (
		backgroundImageWhite: $twitter-icon-white,
		backgroundImageGrey: $twitter-icon-grey,
		backgroundImageColor: $twitter-icon-color,
	),
	instagram: (
		backgroundImageWhite: $instagram-icon-white,
		backgroundImageGrey: $instagram-icon-grey,
		backgroundImageColor: $instagram-icon-color,
	),
	linkedin: (
		backgroundImageWhite: $linkedin-icon-white,
		backgroundImageGrey: $linkedin-icon-grey,
		backgroundImageColor: $linkedin-icon-color,
	)
);

@each $network, $style in $social-networks {
	.social-network-#{$network}) {
		&.social-white {
			.static-background-image {
	      background-image: map-get($style, backgroundImageWhite);
	    }
		}
		&.social-grey {
			.static-background-image {
	      background-image: map-get($style, backgroundImageGrey);
	    }
		}
    .hover-background-image {
      background-image: map-get($style, backgroundImageColor);
    }
	}
}

.social-network-item {
	@extend %change-background-images-with-spans;
	@extend %display-ib;
	margin-right: #{$gutter-padding};
	.social-network-link {
		@extend %link-block;
		position: relative;
		z-index: 3;
		cursor: pointer;
	}
}
