%container {
  max-width: 90%;
  margin: 0 auto;
  display: flex;
  flex-wrap: nowrap;
}

body {
  color: black;
  font-family: 'Futura';
  font-size: 18px;
  &.w-banners {
    overflow: hidden;
  }
}

strong {
  font-family: 'FuturaBold';
}

img {
  max-width: 100%;
}

.full-width-media {
  width: 100%;
  height: auto;
}

.map-z {
  background-color: white;
  position: relative;
  z-index: 4;
}

.pointer-arrow {
  width: 36px;
  height: 36px;
  background-image: url('../images/point-arrow.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: fixed;
  bottom: 24px;
  left: calc(50% - 36px / 2);
  opacity: 0;
  animation: flash 500ms ease-out 3;
  pointer-events: none;
  @media( min-width: $screen-sm-min ) {
    display: none;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  transform: translate3d(0, 0, 0 ); 
  transition: transform 500ms ease-out;
  &.hide {
    transform: translate3d(0, -100%, 0 ); 
  }
  &-container {
    @extend %container;
    padding: 34px 0;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
}

.menu {
  &-toggle {
    position: fixed;
    top: 41px;
    right: 5%;
    z-index: 12;
    cursor: pointer;
    transform: translate3d(0, 0, 0 ); 
    transition: transform 500ms ease-out;
    &.hide {
      transform: translate3d(0, -107px, 0 ); 
    }
    &.active {
      .menu-top {
        @include animation( menuTop 500ms ease-out forwards);
      }
      .menu-bottom {
        @include animation( menuBottom 500ms ease-out forwards);
      }
      .menu-center {
        @include animation( menuCenter 500ms ease-out forwards);
      }
    }
  }
  &-top{
    transform-origin: center;
    @include animation( menuTopRev 500ms ease-out forwards);
  }
  &-bottom {
    transform-origin: center;
    @include animation( menuBottomRev 500ms ease-out forwards);
  }      
  &-center {
    transform-origin: center;
    @include animation( menuCenterRev 500ms ease-out forwards);
  }
}

@include keyframes(menuTop) {
  0% {
    transform: translate3d( 0, 0, 0 );
  }
  50% {
    transform: translate3d( 0, 11px, 0 );
  }
  100% {
    transform: translate3d( 0, 11px, 0 ) rotate(-45deg);
  }
}

@include keyframes(menuBottom) {
  0% {
    transform: translate3d( 0, 0, 0 );
  }
  50% {
    transform: translate3d( 0, -11px, 0 );
  }
  100% {
    transform: translate3d( 0, -11px, 0 ) rotate(45deg);
  }
}

@include keyframes(menuCenter) {
  0% {
    opacity: 1; 
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@include keyframes(menuTopRev) {
  0% {
    transform: translate3d( 0, 11px, 0 ) rotate(-45deg);
  }
  50% {
    transform: translate3d( 0, 11px, 0 );
  }
  100% {
    transform: translate3d( 0, 0, 0 );
  }
}

@include keyframes(menuBottomRev) {
  0% {
    transform: translate3d( 0, -11px, 0 ) rotate(45deg);
  }
  50% {
    transform: translate3d( 0, -11px, 0 );
  }
  100% {
    transform: translate3d( 0, 0, 0 );
  }
}

@include keyframes(menuCenterRev) {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1; 
  }
}

.nav {
  background-color: white;
  padding-right: 5%;
  z-index: 11;
  transition: opacity 250ms ease-out;
  pointer-events: none;
  transform: translate3d(100%, 0, 0);
  transition: transform 500ms ease-out;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  @media( max-width: $screen-xs-max ) {
    left: 0;
    bottom: 0;
    padding-right: 0;
  }
  &.active {
    pointer-events: all;
    transform: translate3d(0, 0, 0);
  }
  .menu-close {
    position: absolute;
    right: 0;
    top: 34px;
    cursor: pointer;
  }
  &-container {
    flex-direction: column;
    padding: 45px;
    position: relative;
    @media( max-width: $screen-xs-max ) {
      max-width: 90%;
      margin: 0 auto;
      padding: 100px 0 0;
    }
    @media( min-width: $screen-sm-min ) {
      padding-right: 100px;
    }    
  }
  &-list {
    font-family: FuturaCondensed;
    font-size: 36px;
    text-transform: uppercase;
    list-style: none;
    border-bottom: 1px solid black;
    padding: 0;
    margin-bottom: 16px;
    &-item {
      margin-bottom: 16px;
      &-link {
        color: black;
        text-decoration: none;
      }
    }
  }
  &-language {
    &-list {
      font-family: FuturaCondensedLight;
      padding: 0;
      list-style: none;
      text-transform: uppercase;
      display: flex;
      &-item {
        font-size: 30px;
        &-link {
          color: black;
          &.active {
            text-decoration: underline;
          }
        }
      }
    }
  }
}

.hero {
  min-height: 100vh;
  max-width: none;
  @extend %container;
  display: flex;
  padding: 56px 5%;
  @media( max-width: $screen-xs-max ) {
    flex-direction: column;
  }    
  &.with-header {
    padding-top: 111px;
    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 5%;
      right: 5%;
      height: 1px;
      background-color: black;
    }
  }
  &-content, &-media {
    @media( min-width: $screen-sm-min ) {
      width: 50%;
      flex-basis: 50%;
    }
  }
  &-media {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    &.centered {
      align-items: center;    
    }
    &-gif {
      max-width: 100%;
      height: auto;
      &.other-gif {
        max-width: 50%;
      }
      &.second-gif {
        max-height: 60vh;
      }
    }
  }
  &-title {
    font-size: 4vw;
    line-height: 1.2em;
    font-family: FuturaCondensedExtraBold;
    text-transform: uppercase;
    @media( max-width: $screen-xs-max ) {
      font-size: 28px;
      margin-bottom: 24px;
      text-align: center;
    }
  }
  &-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @media( min-width: $screen-sm-min ) {
      padding-right: 50px;
    }
    &.centered {
      justify-content: center;
      @media( max-width: $screen-xs-max ) {
        align-items: center;
      }
      .hero-content-top {
        margin-bottom: 150px;
        @media( max-width: $screen-xs-max ) {
          margin-bottom: 34px;
        }      
      }
    }
  }
  &-description {
    font-family: 'FuturaBook';
    font-size: 2vw;
    line-height: 1.2em;
    margin: 0;
    @media( max-width: $screen-xs-max ) {
      font-size: 18px;
      text-align: center;
    }
    &.small {
      > strong {
        text-transform: none;
      }
    }      
    &.uppercase {
      text-transform: uppercase;
    }
    .vs {
      position: relative;
      &:before {
        content: '';
        position: absolute;
        height: 100%;
        width: 100%;
        background-image: url('../images/vs.svg');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        background-position: center 75%;
      }
    }
    strong {
      font-family: 'FuturaHeavy';
      text-transform: uppercase;
    }
  }
}

.recipe {
  background-color:white;
  color: white;
  overflow: hidden;
  transition: background-color 500ms ease-out;
  visibility: visible !important;
  @media( max-width: $screen-xs-max ) {
    padding-bottom: 24px;
  }    
  @media( min-width: $screen-sm-min ) {
    height: 100vh;
  }  
  &.animated {
    background-color: #cc3524;
  }
  &-container {
    @extend %container;
    flex-direction: column;
    @media( min-width: $screen-md-min ) {
      max-width: 75%;
    }
    @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
      max-width: 82%;
    }
  }
  &-content {
    height: 70%;
    display: flex;
    align-items: center;
    @media( min-width: $screen-sm-min ) {    
      margin-left: 12.5%;
    }
    @media( max-width: $screen-xs-max ) {
      height: 400px;
      margin-left: 5%;
    }      
  }
  &-title {
    font-size: 5vw;
    font-family: 'FuturaCondensedExtraBold';
    text-transform: uppercase;
    margin: 0;
    @media( max-width: $screen-xs-max ) {
      font-size: 50px;
      max-width: 40%;
      line-height: 1.2em;
      position: relative;
      z-index: 2;
    }
    @media( max-width: $screen-xxs-max ) {
      font-size: 40px;
    }      
    strong {
      font-family: 'FuturaCondensedExtraBold';
      text-decoration: underline;
    }
  }
  &-media {
    @media( min-width: $screen-sm-min ) {
      margin-right: -300px;
    }
    @media( max-width: $screen-xs-max ) {
      position: absolute;
      right: 0;
      z-index: 1;
      max-width: 80%;
    }
  }
  &-list {
    display: flex;
    align-content: center;
    flex-wrap: no-wrap;
    @media( max-width: $screen-xs-max ) {
      flex-wrap: wrap;
      align-content: center;
      justify-content: center;
    }
    &-item {
      width: 25%;
      flex-basis: 25%;
      font-family: 'FuturaCondensed';
      font-size: 2.4vw;
      line-height: 1.4em;
      letter-spacing: 4px;
      text-transform: uppercase;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
        font-size: 2vw;
      }
      @media( max-width: $screen-xs-max ) {
        width: 40%;
        flex-basis: 40%;
        font-size: 24px;
      }      
      @media( max-width: $screen-xxs-max ) {
        font-size: 18px;
        letter-spacing: 2px;
        padding: 30px 16px !important;
      }        
      &.big {
        border: 3px solid white;
        padding: 30px 0;
        &:not(:last-child) {
          cursor: pointer;
        }
        .break {
          width: 100%;
          flex-basis: 100%;
        }
      }
      &.small {
        width: 12.5%;
        font-size: 50px;
        flex-basis: 12.5%;
        @media( max-width: $screen-xs-max ) {
          width: 20%;
          flex-basis: 20%;
        }      
        @media( max-width: $screen-xxs-max ) {
          padding: 0 !important;
        }        
        &.equal {
          @media( max-width: $screen-xs-max ) {
            width: 100%;
            flex-basis: 100%;
          }          
        }
      }
    }
  }
}

.ssd, .experiences {
  align-items: center;
  min-height: 0;
  @media( min-width: $screen-sm-min ) {
    min-height: 100vh;
  }
  &-content {
    display: flex;
    justify-content: space-between;
    &-top {
      &.special {
        @media( min-width: $screen-sm-min ) {
          margin-bottom: 100px;
        }    
      }
    }
  }
  &-media {
    width: 100%;
    @media( max-width: $screen-xs-max ) {
      margin-top: 24px;
    }
  }
  &-title, &-subtitle {
    font-family: 'FuturaCondensedExtraBold';
    text-transform: uppercase;
  }
  &-title {
    font-size: 3.7vw;
    line-height: 1.2em;
    letter-spacing: 3px;
    @media( max-width: $screen-xs-max ) {
      font-size: 34px;
    }
  }
  &-subtitle {
    font-size: 1.7vw;
    @media( max-width: $screen-xs-max ) {
      font-size: 20px;
    }
  }
  &-description {
    font-family: 'Futurabook';
    font-size: 1.4vw;
    line-height: 1.2em;
    @media( max-width: $screen-xs-max ) {
      font-size: 16px;
    }
    &.heavy {
      font-family: 'FuturaHeavy';
    }
  }
  &-logo {
    max-width: 50%;
    height: auto;
    @media( max-width: $screen-xs-max ) {
      max-width: 200px;
    }      
  }
}

.quotes {
  @extend %container;
  max-width: none;
  padding: 0 5%;
  @media( max-width: $screen-sm-max ) {
    flex-direction: column;
  }    
  &-column {
    width: 48%;
    flex-basis: 48%;
    @media( min-width: $screen-md-min ) {
      padding-right: 50px;
    }
    @media( min-width: $screen-lg-min ) {
      padding-right: 100px;
    }
    @media( max-width: $screen-sm-max ) {
      width: 100%;
      flex-basis: 100%;
      overflow: hidden;
    }      
    &:last-child {
      margin-left: auto;
    }
  }
  &-title {
    font-family: 'FuturaCondensedExtraBold';
    font-size: 32px;
    text-transform: uppercase;
    line-height: 1.4em;
    min-height: 2.8em;
    @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
      font-size: 24px;
    }      
    @media( max-width: $screen-xs-max ) {
      font-size: 20px;
      text-align: center;
    }      
  }
  &-list {
    height: 100%;
    padding: 0; 
    display: flex;
    flex-direction: column;
    list-style: none;
    @media( max-width: $screen-xs-max ) {
      position: relative;
    }
    &-slider {
      overflow: hidden;
      &-wrapper {
        @media( max-width: $screen-xs-max ) {
          position: relative;
          padding: 0 50px;
        }
      }
      &:focus {
        outline: 0;
      }
      &-arrows {
        @media( max-width: $screen-xs-max ) {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
        }
        .slick-arrow {
          @media( max-width: $screen-xs-max ) {
            text-indent: -9999px;
            position: absolute;
            top: calc( 50% - 32px / 2);
            width: 32px;
            height: 32px;
            background-image: url('../images/logan-arrow.svg');
            background-repeat: no-repeat;
            background-size: contain;
            cursor: pointer;
          }
          &.slick-prev {
            left: 0;
          }
          &.slick-next {
            transform: rotate(180deg);
            right: 0;
          }
        }
      }
    }
    &-item {
      height: 25%;
      display: flex;
      flex-wrap: nowrap; 
      margin-bottom: 16px;
      @media( min-width: $screen-md-min ) and ( max-width: $screen-md-max ) {
        margin-bottom: 24px;
      }        
      &:nth-child(2) {
        height: 25%;
      }
      &:nth-child(3) {
        height: 50%;
      }
      &:focus {
        outline: 0;
      }
      @media( max-width: $screen-xs-max ) {
        float: left;
        flex-direction: column;
        align-items: center;
      }
    }
  }
  &-item {
    &-media {
      width: 120px;
      flex-basis: 120px;
      @media( max-width: $screen-sm-max ) {
        width: 90px;
        flex-basis: 90px;
      }        
      @media( max-width: $screen-xs-max ) {
        width: 200px;
        flex-basis: 30px;
      }        
      img {
        max-width: 100%;
      }
    }
    &-content {
      font-size: 20px;
      width: calc( 100% - 120px );
      flex-basis: calc( 100% - 120px );
      line-height: 1.4em;
      @media( min-width: $screen-md-min ) and ( max-width: $screen-md-max ) {
        font-size: 18px;
      }        
      @media( min-width: $screen-sm-min ) {
        padding-left: 24px;
      }        
      @media( max-width: $screen-xs-max ) {
        font-size: 16px;
        width: 100%;
        flex-basis: 100%;
        text-align: center;
      }      
      .heavy {
        font-family: 'FuturaHeavy';
      }
      .book {
        font-family: 'FuturaBookOblique';
      }
    }
  }
}

.banners {
  align-items: center;
  padding: 0 0 0 5%;  
  display: flex;
  z-index: 6;
  margin-bottom: 100px;
  @media( max-width: $screen-xs-max ) {
    padding: 0 5%;  
    min-height: 100vh;
    margin-bottom: 30px;
  }    
  &.active {
    @media( max-width: $screen-xs-max ) {
      z-index: 10000;
    }
  }
  &-content, &-media {
    width: 50%;
    height: 100%;
    flex-basis: 50%;
    height: 100%;
    @media( max-width: $screen-xs-max ) {
      width: 100%;
      flex-basis: 100%;
    }
  }
  &-content {
    display: flex;
    flex-direction: column;
  }
  &-media {
    @media( max-width: $screen-xs-max ) {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      pointer-events: none;
      opacity: 0;
      z-index: 10;
      transition: opacity 500ms ease-out;
    }
    &.active {
      opacity: 1;
      transition: opacity 500ms ease-out 250ms;
      pointer-events: all;
      .banners-media-close {
        opacity: 1;
        transition: opacity 500ms ease-out 250ms;
        pointer-events: all;
      }
    }
    &-close {
      position: absolute;
      width: 28px;
      height: 28px;
      top: 41px;
      right: 5%;
      background-image: url('../images/close.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      opacity: 0;
      z-index: 11;
      transition: opacity 500ms ease-out 0;
      cursor: pointer;
    }
  }
  &-description {
    font-size: 2vw;
    margin-bottom: 50px;
    font-family: 'FuturaCondensedExtraBold';
    text-transform: uppercase;
    @media( max-width: $screen-sm-max ) {
      font-size: 2.5vw;
    }      
    @media( max-width: $screen-xs-max ) {
      font-size: 18px;
    }      
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 48px;      
    @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
      padding: 0 24px;
    }
    @media( max-width: $screen-xs-max ) {
      padding: 0;
    }    
    &-item {
      font-family: 'FuturaCondensed';
      font-size: 26px;
      text-transform: uppercase;
      width: 48%;
      height: 200px;
      margin-bottom: 4%;
      border: 8px solid black;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      @media( max-width: $screen-lg-max ) {
        font-size: 22px  
      }
      @media( max-width: $screen-md-max ) {
        padding: 0 16px;
        text-align: center;
      }
      @media( max-width: $screen-sm-max ) {
        height: 150px;
      }
      @media( max-width: $screen-xs-max ) {
        border-width: 5px;
      }        
      > span {
        text-align: center;
        max-width: 90px;
        @media( max-width: $screen-lg-max ) {
          max-width: 70px;
        }        
      }
      &.yellow {
        border-color: #fcbb1b;
        color: #fcbb1b;
      }
      &.blue {
        border-color: #1f355e;
        color: #1f355e;
      }
      &.red {
        border-color: #cc3524;
        color: #cc3524;
      }
    }
  }
}

.banners {
  &-slider {
    overflow: hidden;
    position: relative;
    @media( max-width: $screen-xs-max ) {
      height: 100%;
    } 
    > .slick-list, > .slick-track {
      @media( max-width: $screen-xs-max ) {
        height: 100%;
      }
      > .slick-track {
        @media( max-width: $screen-xs-max ) {
          height: 100%;
        } 
      }
    }     
    > .slick-arrow {
      text-indent: -9999px;
      position: absolute;
      width: 48px;
      height: 48px;
      top: calc( 50% - 48px / 2);
      text-indent: -9999px;
      background-image: url('../images/logan-arrow-white.svg');
      background-repeat: no-repeat;
      background-size: contain;
      z-index: 10;
      cursor: pointer;
      @media( max-width: $screen-sm-max ) {
        width: 24px;
        height: 24px;
        top: calc( 50% - 24px / 2);
      }        
      &.slick-prev {
        left: 24px; 
      }
      &.slick-next {
        right: 24px;
        transform: rotate(180deg);
      }
    }
    > .slick-dots {
      position: absolute;
      bottom: 48px;
      left: 50%;
      transform: translate3d( -50%, 0 ,0 );
      display: flex;
      > li {
        text-indent: -9999px;
        width: 12px;
        height: 12px;
        border-radius: 100%;
        border: 1px solid white;
        transition: all 500ms ease-out;
        margin: 0 5px;
        cursor: pointer;
        &:hover {
          background-color: white;
        }
        &.slick-active {
          background-color: white;
        }
      }
    }
    &-item {
      padding: 48px 0 96px;
      float: left;
      background-color: black;
      @media( max-width: $screen-xs-max ) {
        height: 100%;
        display: flex;
        align-items: center;
        padding: 0;
        border: 5px solid black;
      }  
      &.yellow {
        background-color: #fcbb1b;
        @media( max-width: $screen-xs-max ) {
          border: 5px solid #fcbb1b;
        }
        .banners-cellphone-title {
          color: #fcbb1b;
        }
      }
      &.blue {
        background-color: #1f355e;
        @media( max-width: $screen-xs-max ) {
          border: 5px solid #1f355e;
        }
        .banners-cellphone-title {
          color: #1f355e;
        }
      }
      &.red {
        background-color: #cc3524;
        @media( max-width: $screen-xs-max ) {
          border: 5px solid #cc3524;
        }
        .banners-cellphone-title {
          color: #cc3524;
        }
      }
      &:focus {
        outline: 0;
      }
    }
  }
  &-cellphone {
    &-wrapper {
      width: 280px;
      margin: 0 auto;
      padding: 142px 24px;
      background-image: url('../images/logan-smart.svg'); 
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      @media( max-width: $screen-sm-max ) {
        width: 200px;
        padding: 64px 8px;
      }        
      @media( max-width: $screen-xs-max ) {
        width: 100%;
        height: 100%;
        background-image: none;
        background-color: white;
        padding: 64px 24px;
      }        
      .slick-arrow {
        text-indent: -9999px;
        position: absolute;
        width: 16px;
        height: 16px;
        left: calc( 50% - 16px / 2);
        text-indent: -9999px;
        background-image: url('../images/logan-arrow.svg');
        background-repeat: no-repeat;
        background-size: contain;
        cursor: pointer;
        &.slick-prev {
          top: 0;
          transform: rotate(90deg);
        }
        &.slick-next {
          bottom: 0;
          transform: rotate(270deg);
        }
      }
    }
    &-slider {
      &-wrapper {
        padding: 16px 0;
        position: relative;
        overflow: hidden;
      }
      &-item {
        float: left;      
      }
    }
    &-title {
      font-size: 22px;
      margin-bottom: 24px;
      text-transform: uppercase;
      font-family: 'FuturaCondensedExtraBold';       
      @media( max-width: $screen-sm-max ) {
        font-size: 16px;
      }        
      @media( max-width: $screen-xs-max ) {
        font-size: 24px;
      }        
    }
    &-list {
      overflow: hidden;
    }
    &-item {
      font-family: 'FuturaCondensedExtraBold';
      font-size: 14px;
      text-transform: uppercase;
      height: 50px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid black;
      justify-content: space-between;
      cursor: pointer;
      @media( max-width: $screen-sm-max ) {
        font-size: 10px;
      } 
      @media( max-width: $screen-xs-max ) {
        font-size: 16px;
        height: 80px;
      }        
      &:focus {
        outline: 0;
      }
      > img {
        width: 30%;
        flex-basis: 30%;
      }
    }
  }
}

@include keyframes( flash ) {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.map {
  height: 100vh;
  position: relative;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  opacity: 1;
  &.hide {
    opacity: 0;
    .svg-map {
      g {
        animation: none;
      }
    }
  }
  &-mask {
    height: 100vh;
    width: 100%;
    position: relative;
    z-index: 1;
  }
  &-data {
    color: black;
    text-transform: uppercase;
    background-color: white;
    font-family: 'FuturaCondensedExtraBold';
    font-size: 24px;
    white-space: nowrap;
    padding: 8px 16px;
    position: absolute;
    top: 50px;
    left: calc(100% - 24px);
    transform: rotate(90deg);
    transform-origin: top left;
    z-index: 3;
    @media( max-width: $screen-xs-max ) {
      font-size: 16px;
    }
    #timer {
      &:before {
        content: '- ';
      }
      &:empty {
        &:before {
          content: ''
        }
      }
    }
    #weather {
      &:before {
        content: '- ';
      }
      &:after {
        content: ' ºC';
      }
      &:empty {
        &:before, &:after {
          content: ''
        }
      }
    }
  }
  &-google, &-content, &-words-wrapper, .svg-map {
    position: absolute;
    width: 100%;
    height: 100%;
    position: absolute !important;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .svg-map {
    width: 1920px;
    height: 1080px;
    > g {
      @include animation( flash 500ms ease-out infinite );
      &.hide {
        display: none;
      }
    }
  }
  &-words {
    &-wrapper {
      z-index: 2;
    }
    &-item {
      opacity: 0;
      &.active {
        opacity: 1;
        transition: opacity 500ms ease-out;
      }
      &.people {
        .map-words-word {
          &:nth-child(1) {
            top: 5%;
            right: 10%;
            animation-delay: 100ms;
          }
          &:nth-child(2) {
            bottom: 10%;
            right: 70%;
            animation-delay: 250ms;
          }
          &:nth-child(3) {
            top: 30%;
            right: 30%;
            animation-delay: 50ms;
          }
          &:nth-child(4) {
            top: 45%;
            left: 15%;
            animation-delay: 200ms;
          }
          &:nth-child(5) {
            top: 67%;
            left: 45%;
            animation-delay: 150ms;
          }
        }
      }
      &.places {
        .map-words-word {
          &:nth-child(1) {
            top: 2%;
            left: 5%;
            animation-delay: 80ms;
          }
          &:nth-child(2) {
            bottom: 20%;
            left: 70%;
            animation-delay: 230ms;
          }
          &:nth-child(3) {
            bottom: 45%;
            right: 10%;
            animation-delay: 30ms;
          }
          &:nth-child(4) {
            top: 75%;
            left: 15%;
            animation-delay: 180ms;
          }
          &:nth-child(5) {
            bottom: 48%;
            right: 50%;
            animation-delay: 130ms;
          }
        }
      }
      &.things {
        .map-words-word {
          &:nth-child(1) {
            top: 5%;
            left: 50%;
            animation-delay: 65ms;
          }
          &:nth-child(2) {
            bottom: 25%;
            left: 33%;
            animation-delay: 215ms;
          }
          &:nth-child(3) {
            bottom: 33%;
            left: 5%;
            animation-delay: 20ms;
          }
          &:nth-child(4) {
            top: 10%;
            right: 30%;
            animation-delay: 165ms;
          }
          &:nth-child(5) {
            top: 57%;
            right: 27%;
            animation-delay: 115ms;
          }
          &:nth-child(6) {
            bottom: 3%;
            right: 3%;
            animation-delay: 67ms;
          }
          &:nth-child(7) {
            top: 23%;
            right: 5%;
            animation-delay: 133ms;
          }
        }
      }
    }
    &-word {
      text-align: center;
      font-family: 'FuturaBold';
      font-size: 14px;
      color: #cc3524;
      word-break: break-all;
      text-transform: uppercase;
      position: absolute;
      opacity: 0;
      @include animation( flash 500ms ease-out infinite );
      @media( max-width: $screen-xs-max ) {
        font-size: 14px;
      }        
    }
  }
  &-content {
    z-index: 3;
    padding: 50px 5%;
  }
  &-title {
    &-line {
      padding: 12px 8px 8px;
      font-family: 'FuturaCondensedExtraBold';
      font-size: 4vw;
      text-transform: uppercase;
      color: white;
      background-color: black;
      line-height: 1.8em;
      @media( max-width: $screen-sm-max ) {
        font-size: 24px;
        line-height: 2.4em;
      }
      @media( max-width: $screen-xs-max ) {
        font-size: 20px;
      }
    }
  }
  &-header {
    display: flex;
    align-items: center;
    margin-bottom: 36px;
    @media( min-width: $screen-sm-min ) {
      padding-left: 40px;
    }
    &-title {
      font-family: 'FuturaCondensedExtraBold';
      font-size: 16px;
      text-transform: uppercase;
    }
  }
  &-filters {
    &-list {
      display: flex;
      @media( min-width: $screen-sm-min ) {
        margin-left: 16px;
      }
    }
    &-item {
      font-family: 'FuturaCondensedExtraBold';
      font-size: 22px;
      background-color: white;
      text-transform: uppercase;
      letter-spacing: 2px;      
      margin: 0 16px;
      padding: 8px 24px;
      border: 4px solid black;
      cursor: pointer;
      box-shadow: 0 0 18px 0 rgba(black, .5);
      transition: all 250ms ease-out;
      &:hover {
        box-shadow: 0 0 25px 0 rgba(black, .7);
      }
      @media( max-width: $screen-xs-max ) {
        font-size: 16px;
        padding: 8px 8px;
        margin: 0 8px;
        border: 4px solid black;
      }        
      &.active {
        opacity: 0.5;
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}