.publishers {
  @extend %container;
  max-width: none;
  padding: 125px 5% 0;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
  &-flex {
    display: flex;
    position: relative;
    align-items: center;
    @media( max-width: $screen-xs-max ) {
      flex-direction: column;
    }      
  }
  &-column {
    &:not(.inside) {
      width: 50%;
      flex-basis: 50%;
      @media( max-width: $screen-xs-max ) {
        width: 100%;
        margin-bottom: 24px;
      }
      &:nth-child(1){ 
        @media( min-width: $screen-md-min ) {
          padding-right: 50px;
        }
      }
      &:nth-child(2){ 
        @media( min-width: $screen-sm-min ) {
          padding-left: 50px;
        }
      }
    }
    &.inside {
      position: relative;
      // height: 63%;
      height: 400px;
      @media( min-width: $screen-md-min ) and ( max-width: $screen-md-max ) {
        height: 320px;
      }        
      @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
        height: 250px;
      }        
      @media( max-width: $screen-xs-max ) {
        height: auto;
      }
        
    }
  }
  &-title {
    font-size: 30px;
    line-height: 1.2em;
    margin-bottom: 36px;
    text-align: left;
    @media( min-width: $screen-md-min ) {
      font-size: 4vw;
    }      
    &-wrapper {
      display: flex;
    }
  }
  &-title, &-subtitle {
    font-family: 'FuturaCondensedExtraBold';
    text-transform: uppercase;
    z-index: 1;
    position: relative;
  }
  &-subtitle {
    text-align: center;
  }
  &-description {
    font-family: 'FuturaBook';
    z-index: 1;
    position: relative;
    line-height: 1.2em;
    @media( min-width: $screen-md-min ) {
      font-size: 2vw;
    }
  }
}

.publishers {
  &-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    height: 315px;
    z-index: 2;
    @media( min-width: $screen-sm-min ) {
      height: 100%;
    }
    &-item {
      background-color: white;
      border: 8px solid black;
      padding: 8px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: absolute;
      transition: all 250ms ease-out;
      cursor: pointer;
      &:nth-child(n+3) {
        bottom: 0;
        top: 52%;
      }
      &:nth-child(-n+2) {
        top: 0;
        bottom: 52%;
      }
      &:nth-child(odd) {
        right: 51%;
        left: 0;
      }
      &:nth-child(even) {
        right: 0;
        left: 51%;
      }
      &:nth-child(n+3) {
        margin-bottom: 0;
      }
      &.active {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        justify-content: center;
        align-items: center;
        border-width: 8px;
        padding: 0 24px;
        .publishers-item-description {
          font-family: 'FuturaBook';
          font-size: 2vw;
          line-height: 1.2em;
          text-align: center;
          @media( max-width: $screen-xs-max ) {
            font-size: 16px;
          }
        }
        .publishers-item-title {
          font-family: 'FuturaCondensedBold';
          font-size: 4vw;
          letter-spacing: 2px;
          line-height: 1.2em;
          margin-bottom: 12px;
          text-transform: uppercase;
          text-align: center;
          min-height: 1.2em;
          @media( max-width: $screen-xs-max ) {
            font-size: 24px;
          }
        }
      }
      &.high {
        z-index: 2;
      }
      &.yellow {
        border-color: #fcbb1b;
        .publishers-item-title {
          color: #fcbb1b;
        }
      }
      &.blue {
        border-color: #1f355e;
        .publishers-item-title {
          color: #1f355e;
        }
      }
      &.red {
        border-color: #cc3524;
        .publishers-item-title {
          color: #cc3524;
        }
      }
    }
  }
  &-item {
    &-title {
      font-size: 2.2vw;
      font-family: 'FuturaCondensedBold';
      letter-spacing: 2px;
      text-transform: uppercase;
      line-height: 1.2em;
      min-height: 2.4em;
      @media( max-width: $screen-xs-max ) {
        min-height: 2.4em;
        font-size: 26px;
        margin-bottom: 16px !important;
      }       
      @media( max-width: $screen-xxs-max ) {
        font-size: 20px;
        min-height: 3.4em;
      }        
    }
    &-description {
      font-family: 'FuturaBook';
      font-size: 1.3vw;
      overflow: hidden;
      line-height: 1.2em;
      @media( max-width: $screen-xs-max ) {
        font-size: 16px;
        line-height: 1.2em;
      }  
      @media( max-width: $screen-xxs-max ) {
        line-height: 1.25em;
        font-size: 14px;
      }        
    }
  }
}

.publishers {
  &-slider {
    background-color: white;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    opacity: 0;
    @include transition( opacity 0 ease-out );
    &.active {
      z-index: 11;
      opacity: 1;
      @include transition( opacity 250ms ease-out 500ms );
    }
    .slick-list, .slick-track {
      height: 100%;
    }
    &-arrows {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 10;
      opacity: 0;
      pointer-events: none;
      @include transition( opacity 250ms ease-out );
      &.active {
        opacity: 1;
        pointer-events: all;
        @include transition( opacity 250ms ease-out 500ms );
      }      
      .slick-arrow {
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        background-image: url('../images/logan-arrow.svg');
        position: absolute;
        top: calc(50% - 26px / 2);
        bottom: 0;
        text-indent: -99999px;
        width: 36px;
        height: 36px;
        cursor: pointer;
        @media( max-width: $screen-xs-max ) {
          width: 20px;
          height: 20px;
        }          
        &.slick-prev {
          left: -45px;
          @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
            left: -30px;
          }
          @media( max-width: $screen-xs-max ) {
            left: -20px;
          }
        }
        &.slick-next {
          right: -45px;
          transform: rotate(180deg);
          @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
            right: -30px;
          }
          @media( max-width: $screen-xs-max ) {
            right: -20px;
          }
        }
      }
    }
    &-item {
      float: left;
      height: 100%;
      display: flex;
      align-content: center;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding: 0 24px;
      border: 8px solid black;
      &.yellow {
        border-color: #fcbb1b;
        .publishers-slider-item-title {
          color: #fcbb1b;
        }
      }
      &.blue {
        border-color: #1f355e;
        .publishers-slider-item-title {
          color: #1f355e;
        }
      }
      &.red {
        border-color: #cc3524;
        .publishers-slider-item-title {
          color: #cc3524;
        }
      }
      &:focus {
        outline: none;
      }
      &-title {
        font-family: 'FuturaCondensedBold';
        font-size: 4vw;
        letter-spacing: 2px;
        line-height: 1.2em;
        margin-bottom: 24px;
        text-transform: uppercase;
        text-align: center;
        @media( max-width: $screen-xs-max ) {
          font-size: 24px;
          margin-bottom: 16px;
        }
      }
      &-description {
        font-family: 'FuturaBook';
        font-size: 2vw;
        line-height: 1.2em;
        text-align: center;
        @media( max-width: $screen-xs-max ) {
          font-size: 16px;
        }
      }
    }
    &-close {
      height: 24px;
      width: 24px; 
      position: absolute;
      right: 12px;
      top: 12px;
      background-image: url('../images/close.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      z-index: 12;
      opacity: 0;
      transition: opacity 0 ease-out;
      cursor: pointer;
      &.active {
        opacity: 1;
        transition: opacity 250ms ease-out 500ms;
      }
    }
  }
}

.publishers {
  &-brands {
    &-list {
      display: flex;
      flex-wrap: wrap;
    }
    &-subtitle {
      font-size: 18px;
      @media( min-width: $screen-md-min ) {
        font-size: 2vw;
      }    
    }
    &-item {
      display: flex;
      align-items: center;
      width: 33.33%;
      flex-basis: 33.33%;
      margin-bottom: 1%;
      justify-content: center;
      img {
        filter: grayscale(100%);
        &.animated {
          filter: grayscale(0);
          transition: filter 1s ease-out 500ms;
        }
      }
      @media( max-width: $screen-xs-max ) {
        width: 25%;
        flex-basis: 25%;
      }        
      &:nth-child(4), &:nth-child(5), &:nth-child(9), &:nth-child(10), &:nth-child(14), &:nth-child(15), &:nth-child(19), &:nth-child(20), &:nth-child(24), &:nth-child(25), &:nth-child(29), &:nth-child(30) {
        @media( min-width: $screen-sm-min ) {
          width: 50%;
          flex-basis: 50%;
        }
        img {
          max-width: 70%;
        }
      }
      &:nth-child(5), &:nth-child(6), &:nth-child(7), &:nth-child(12), &:nth-child(13), &:nth-child(14), &:nth-child(20), &:nth-child(21) {
        @media( max-width: $screen-xs-max ) {
          width: 33.33%;
          flex-basis: 33.33%;
        }
      }        
    }
  }
}