// Normalize Styles
@import 'node_modules/normalize.css/normalize';

// Import Standards

@import 'standards/reset';
@import 'standards/variables';
@import 'standards/breakpoints';
@import 'standards/colors';
@import 'standards/mixins';
@import 'standards/extends';
@import 'standards/grid-settings';
@import 'standards/social-icons';
@import 'standards/animate';

@import 'fonts';
@import 'home';
@import 'about';
@import 'technology';
@import 'publishers';
@import 'contact';

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.yeogurt-info {
  text-align: center;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.version {
  font-size: 14px;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.logo {
  margin-top: 15px;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}
