.wbb {
  padding-bottom: 48px;
  margin-bottom: 48px;
  border-bottom: 1px solid black;
  img {
    height: 50px;
    width: auto;
    @media( max-width: $screen-xs-max ) {
      height: 40px;
    }    
    &.nodus {
      max-width: 70px;
    }
  }
}

.about {
  @extend %container;
  display: flex;
  padding-top: 125px;
  overflow: hidden;
  @media( max-width: $screen-xs-max ) {
    display: block;
    position: relative;
    max-width: 100%;
    padding: 125px 0 0 5%;
    overflow: hidden;
  }
  &-column {
    width: 50%;
  }
  &-header {
    @media( max-width: $screen-xs-max ) {
      position: absolute;
      top: 125px;
      left: 5%;
      right: 30%;
      z-index: 5;
    }
  }
  &-subtitle, &-title {
    font-family: 'FuturaCondensedExtraBold';
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #cc3524;
  }
  &-title {
    font-size: 4vw;
    @media( max-width: $screen-sm-max ) {
      font-size: 34px;
    }
  }
  &-subtitle {
    font-size: 1.5vw;
    line-height: 1.2em;
    @media( max-width: $screen-sm-max ) {
      font-size: 16px;
    }    
  }
  &-description {
    font-size: 1.5vw;
    line-height: 1.6em;
    @media( max-width: $screen-sm-max ) {
      font-size: 16px;
    }    
  }
  &-media {
    @media( max-width: $screen-xs-max ) {
      float: right;
      width: 50%;
    }
  }
  .floated {
    @media( max-width: $screen-xs-max ) {
      padding-top: 155px;
    } 
    @media( max-width: $screen-xxs-max ) {
      padding-top: 170px;
    } 
  }
}

.about-numbers {
  @extend %container;
  display: flex;
  position: relative;
  &:before {
    content: '';
    background-color: black;
    height: 1px;
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    @media( max-width: $screen-xs-max ) {
      left: 5%;
      right: 5%;
      bottom: 5px;
    }      
  }
  &-column {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    @media( max-width: $screen-xs-max ) {
      width: 100%;
      flex-basis: 100%;
    }      
    &.media {
      @media( min-width: $screen-sm-min ) {
        padding: 0 36px;
      }
      @media( max-width: $screen-xs-max ) {
        align-items: flex-start;
        margin-left: -36px;
      }
    }
  }
  &-list {
    list-style: none;
    display: flex;
    flex-direction: column;
    height: 90%;
  }
  &-item {
    font-family: 'FuturaCondensedBold';
    text-transform: uppercase;
    font-size: 30px; 
    height: 100%;
    text-align: right;
    font-size: 3vw;
    @media( max-width: $screen-xs-max ) {
      font-size: 30px; 
    } 
    @media( max-width: $screen-xxs-max ) {
      font-size: 24px; 
    }      
    .highlight {
      color: #cc3524;
      font-family: 'FuturaCondensedExtraBold';
    }
  }
  &-media {
    max-width: 35%;
    height: auto;
    margin-left: auto;
    @media( max-width: $screen-xs-max ) {
      max-width: 100px;
      margin-right: 10%;
    }      
  }
  &-image {
    @media( max-width: $screen-xs-max ) {
      max-width: 70%;
    }      
  }
  .absolute {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    @media( min-width: $screen-xs-min ) {
      padding-bottom: 50px;
    }      
    @media( max-width: $screen-xs-max ) {
      width: 100%;
      padding-bottom: 20px;
    } 
  }
}

.about-list {
  @extend %container;
  display: flex;
  flex-direction: column;
  &.management {
    font-size: 16px;
    .about-list-subtitle {
      font-size: 34px;      
      @media( min-width: $screen-md-min ) and ( max-width: $screen-md-max ) {
        font-size: 28px;
      }
      @media( min-width: $screen-sm-min ) and ( max-width: $screen-sm-max ) {
        font-size: 24px;
      }
      @media( max-width: $screen-xs-max ) {
        font-size: 24px;
      }        
    }
    .about-list-description {
      font-size: 20px;
      line-height: 1.6em;
      @media( min-width: $screen-sm-min ) and ( max-width: $screen-md-max ) {
        font-size: 18px;
      }
      @media( max-width: $screen-xs-max ) {
        font-size: 16px;
      }      
    }
  }
  &.flags {
    .about-list-subtitle, .about-list-description {
      font-size: 22px;
      @media( min-width: $screen-sm-min ) and ( max-width: $screen-md-max ) {
        font-size: 20px;
      }        
      @media( max-width: $screen-xs-max ) {
        font-size: 16px;
      }        
    }
  }
  &-flex {
    display: flex;
    justify-content: space-between;
    @media( max-width: $screen-xs-max ) {
      flex-direction: column;
    }
  }
  &-column {
    width: 32%;
    flex-basis: 32%; 
    @media( max-width: $screen-xs-max ) {
      width: 100%;
      flex-basis: 100%;
      margin-bottom: 24px;
    }
  }
  &-title {
    font-size: 34px;
    text-transform: uppercase;
    color: #cc3524;
    @media( max-width: $screen-xs-max ) {
      font-size: 28px;
    }      
  }
  &-title, &-subtitle {
    font-family: 'FuturaCondensedBold';
    letter-spacing: 1.5px;
  }
  &-subtitle {
    &:not(.position) {
      text-transform: uppercase;
    }
  }
  &-description {
    font-family: "FuturaBook";
    line-height: 1.4em;
    @media( max-width: $screen-xs-max ) {
      font-size: 16px;
    }
  }
  &-link {
    font-family: 'FuturaCondensedExtraBold';
    text-transform: uppercase;
    letter-spacing: 1.5px;
    text-align: right;
    float: right;
  }
  &-list-item {
    line-height: 1.4em;
    &:before {
      font-family: 'FuturaBook';
      content: '» ';
      color: #cc3524;
    }
  }
  &-management-media {
    height: auto !important;
    max-width: 40%;
    height: auto;
    margin-bottom: 8px;
    @media( max-width: $screen-xs-max ) {
      max-width: 80px;
    }      
  }
  &-image {
    max-width: 60%;
    &.image-logo {
      margin-bottom: 8px !important;
    }
  }
  &-flags-media {
    max-width: 50px;
    height: auto;
    margin-bottom: 12px;
    @media( max-width: $screen-xs-max ) {
      max-width: 50px;
    }      
  }
}