%clearfix {
	&:after {
		display: block;
		clear: both;
		content: '';
	}
}

%h-align {
	left: 50%;
	@include transform(translate3d( -50%, 0, 0) scale3d(1, 1, 1));
  zoom: 1.01;
}

%v-align {
	top: 50%;
	@include transform(translate3d( 0, -50%, 0) scale3d(1, 1, 1));
  zoom: 1.01;
}

%v-h-align {
	left: 50%;
	top: 50%;
	@include transform(translate3d( -50% , -50%, 0) scale3d(1, 1, 1));
  zoom: 1.01;
}

%all-corners {
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

%display-ib {
	display: inline-block;
	vertical-align: middle
}

%background-image {
	background-repeat: no-repeat;
	background-position: center;
	background-size: 100%;
}

%change-background-images-with-spans {
	position: relative;
	.static-background-image, .hover-background-image {
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
		position: absolute;
		@extend %all-corners;
		@include transition( opacity $main-transition-time ease-in );
	}
	.static-background-image {
		opacity: 1;
	}
	.hover-background-image {
		opacity: 0;
	}
	&:hover {
		.static-background-image {
			opacity: 0;
		}
		.hover-background-image {
			opacity: 1;
		}
	}
}

%standard-transition {
	@include transition( all $main-transition-time ease );
}

%link-block {
	height: 100%;
	display: block;
}

%text-center {
	text-align: center;
}

%text-right {
	text-align: right;
}

%no-selection {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -html-user-select: none;    /* Konqueror */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;
}
