// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body, h1, h2, h3, h4, h5, h6, p, ul, li, button, input {
	padding: 0;
	margin: 0;
	border: 0;
	font-weight: 400;
	font-size: inherit;
}

ul, li {
	list-style: none;
}

a {
	color: inherit;
	text-decoration: none;
	&:hover {
		color: inherit;
		text-decoration: none;
	}
}

button, input {
	font-family: inherit;
	background-color: transparent;
}

input {
	background-color: transparent;
}

button, input, textarea, select {
	-webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
	border: 0;
	&:focus {
		outline: none;
	}
}

select {
	-webkit-appearance: none;
  -webkit-border-radius: 0px;
	border: none;
}
